// src/services/api.js
import axios from 'axios'

const api = axios.create({
  baseURL: process.env.NODE_ENV === 'production'
    ? 'http://localhost/SmartScribe-main/public'
    : 'http://localhost/SmartScribe-main/public'
})

// Add auth token to requests
api.interceptors.request.use(config => {
  const token = localStorage.getItem('token')
  if (token) {
    config.headers.Authorization = `Bearer ${token}`
  }

  // Add user ID from localStorage for authentication
  const userData = localStorage.getItem('user')
  if (userData) {
    try {
      const user = JSON.parse(userData)
      if (user && user.id) {
        config.headers['X-User-ID'] = user.id
      }
    } catch (error) {
      // Error parsing user data
    }
  }

  // Don't set Content-Type for FormData - let axios handle it
  if (config.data instanceof FormData) {
    delete config.headers['Content-Type']
  } else if (config.data && typeof config.data === 'object') {
    // Ensure JSON requests have proper Content-Type
    config.headers['Content-Type'] = 'application/json'
  }

  return config
})

// Add response interceptor
api.interceptors.response.use(
  response => {
    return response
  },
  error => {
    return Promise.reject(error)
  }
)

export default {
  // Auth
  login(credentials) {
    // Explicitly stringify the data to ensure proper JSON format
    return api.post('/SmartScribe-main/public/index.php?resource=auth&action=login', JSON.stringify(credentials), {
      headers: {
        'Content-Type': 'application/json'
      }
    })
  },
  register(userData) {
    // Explicitly stringify the data to ensure proper JSON format
    return api.post('/SmartScribe-main/public/index.php?resource=auth&action=register', JSON.stringify(userData), {
      headers: {
        'Content-Type': 'application/json'
      }
    })
  },
  logout() {
    return api.post('/SmartScribe-main/public/index.php?resource=auth&action=logout')
  },
  getUser() {
    return api.get('/SmartScribe-main/public/index.php?resource=auth&action=profile')
  },
  updateProfile(profileData) {
    // Explicitly stringify the data to ensure proper JSON format
    return api.put('/SmartScribe-main/public/index.php?resource=auth&action=profile', JSON.stringify(profileData), {
      headers: {
        'Content-Type': 'application/json'
      }
    })
  },
  uploadProfilePicture(formData) {
    return api.post('/SmartScribe-main/public/index.php?resource=auth&action=upload-profile-picture', formData)
  },
  
  // Notes
  getNotes() {
    return api.get('/SmartScribe-main/public/index.php?resource=notes')
  },
  getNote(id) {
    return api.get(`/SmartScribe-main/public/index.php?resource=notes&id=${id}`)
  },
  createNote(noteData) {
    // If there's an image file, we need to send FormData
    if (noteData.image && noteData.image instanceof File) {
      const formData = new FormData()
      formData.append('title', noteData.title)
      formData.append('text', noteData.text)
      formData.append('image', noteData.image)

      // Don't manually set Content-Type for FormData - let axios handle it
      return api.post('/SmartScribe-main/public/index.php?resource=notes', formData)
    } else {
      // For text-only notes, send as JSON
      return api.post('/SmartScribe-main/public/index.php?resource=notes', {
        title: noteData.title,
        text: noteData.text
      })
    }
  },
  updateNote(id, noteData) {
    // Convert JSON data to FormData to match backend expectations
    const formData = new FormData()
    formData.append('title', noteData.title)
    formData.append('text', noteData.text)
    if (noteData.summary) {
      formData.append('summary', noteData.summary)
    }
    if (noteData.keywords) {
      formData.append('keywords', noteData.keywords)
    }

    return api.post(`/SmartScribe-main/public/index.php?resource=notes&id=${id}`, formData)
  },
  deleteNote(id) {
    return api.delete(`/SmartScribe-main/public/index.php?resource=notes&id=${id}`)
  },
  
  // OCR
  scanImage(formData) {
    return api.post('/SmartScribe-main/public/ocr', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
  },
  
  // Summaries
  generateSummary(noteId, options) {
    return api.post(`/SmartScribe-main/public/notes/${noteId}/summaries`, options)
  },

  // Summaries
  getSummaries() {
    return api.get('/SmartScribe-main/public/summaries')
  },
  getSummary(id) {
    return api.get(`/SmartScribe-main/public/summaries/${id}`)
  },
  createSummary(noteId, options) {
    return api.post(`/SmartScribe-main/public/summaries`, { note_id: noteId, ...options })
  },

  // Progress
  getProgressStats() {
    return api.get('/SmartScribe-main/public/progress/stats')
  },

  // Dashboard
  getDashboardStats() {
    return api.get('/SmartScribe-main/public/index.php?resource=dashboard&action=stats')
  },

  // Settings
  getSettings() {
    return api.get('/SmartScribe-main/public/settings')
  },
  updateSettings(settings) {
    // Explicitly stringify the data to ensure proper JSON format
    return api.put('/SmartScribe-main/public/settings', JSON.stringify(settings), {
      headers: {
        'Content-Type': 'application/json'
      }
    })
  },

  // GPT AI Services
  gpt: {
    generateSummary(text, options = { length: 'medium' }) {
      return api.post('/SmartScribe-main/public/index.php?resource=gpt&action=generateSummary', { text, ...options })
    },
    generateQuiz(text, options = { difficulty: 'medium', questionCount: 5 }) {
      return api.post('/SmartScribe-main/public/index.php?resource=gpt&action=generateQuiz', { text, ...options })
    },
    extractKeywords(text, count = 5) {
      return api.post('/SmartScribe-main/public/index.php?resource=gpt&action=extractKeywords', { text, count })
    }
  },

  // Quizzes
  getQuizzes() {
    return api.get('/SmartScribe-main/public/quizzes')
  },
  getQuiz(id) {
    return api.get(`/SmartScribe-main/public/quizzes/${id}`)
  },
  createQuiz(noteId, options) {
    return api.post(`/SmartScribe-main/public/quizzes`, { note_id: noteId, ...options })
  },
  updateQuiz(id, data) {
    return api.put(`/SmartScribe-main/public/quizzes/${id}`, data)
  },
  generateQuiz(noteId, options) {
    return api.post(`/SmartScribe-main/public/notes/${noteId}/quizzes`, options)
  }
}